@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.navigation__menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

.navigation__item {
  display: inline-block;
  border-right: 1px solid #bbb;
}

.navigation__item a {
  display: block;
  color: white;
  text-align: center;
  padding: 18px 20px;
  text-decoration: none;
}

.navigation__item a:hover {
  background-color: #111;
}

.home__wrapper {
  width: 1000px;
  min-height: 600px;
  margin: 0 auto;
}

.home__image,
.home__title {
  padding: 20px;
  text-align: center;
}
.signForm {
  margin: 10px auto;
  padding: 20px;
  width: 500px;
  min-height: 400px;
  font-family: 'Roboto', sans-serif;
  background-color: #000;
}

.signForm input {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: grey;
  background-color: #000;
  border: 1px solid grey;
  padding: 10px;
  margin: 20px;
}

.signForm__logo {
  color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 50px;
  font-weight: 700;
  font-size: 24px;
}

.signForm__image {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background-color: pink;
  margin-bottom: 10px;
}

.signForm__logo img {
  width: 30px;
  height: 30px;
}

.signForm__name {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 100%;
}

.signForm__name input {
  width: 45%;
}

.signForm_validation {
  width: 88%;
}

.signForm_validation input {
  width: 100%;
  margin-top: 10px;
}

.signForm__rememberMe,
.signForm__promotion {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0;
  margin: 0;
}

.signForm__links a {
  color: aquamarine;
}

.signForm__submit {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 90%;
  margin: 10px auto;
  padding: 10px;
  background-color: aquamarine;
}

.signForm__links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 20px;
}


